import React from "react"

function Header(){
    return(
    <div className="header-container">
        <div className="row">
            <div className="col-4">

                <div className="gamelist-text">
                    <span>Choose your game</span>
                </div>
                
                <div className="gamelist-arrow">
                    <img className="arrow" src="./arrow.png"/>
                </div>
                
            </div>

            <div className="col-4">
                <img className="logo" src="./plugynLogo.png"/>
            </div>
        
            <div className="col-4">
                <span className="loginText">Log in</span>
                <span className="signupText">New around? Sign up here</span>
            </div>
        </div>
    </div>
        
      
    )
}


export default Header